import * as Yup from "yup";

//validation form
const infoOrderSchema = Yup.object().shape({
    name: Yup.string()
      .min(2, "Ln hn 2 k t!")
      .max(50, "Tn qu di!")
      .required("Vui lng nhp h v tn!"),
    phone: Yup.string()
      .min(10, "Khng ng nh dng!")
      .max(10, "Khng ng nh dng!")
      .required("Vui lng nhp s in thoi."),
    email: Yup.string()
      .email("Email khng ng nh dng.")
      .required("Vui lng nhp email."),
});

export {
    infoOrderSchema,   
}